@import url(https://fonts.googleapis.com/css?family=Lato:400,700&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#root {
	height: 100%;
}

@font-face {
  font-family: "line-awesome";
  src: url("/line-awesome/line-awesome/fonts/line-awesome.ttf");
}

 html, body {
  height: 100%;
  width: 100vw;
  min-height: 0;
  font-size: 10px;
  font-family: "line-awesome";
}

.App {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  /*overflow-y: hidden;*/
}

.navbar {
  height: 80px;
  min-height: 80px;
  margin: 0;
  position: relative;
  z-index: 2;
  background-color: white;
}

.navbar::before {
  content: unset;
}

.navbar__wrapper {
  width: 90%;
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar__list {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.navbar__brand {
  font-size: 3rem;
  margin: 0;
  color: white;
  font-weight: bold;
  letter-spacing: -0.25rem;
}

.navbar__list > li {
  font-size: 2rem;
  margin: 0 20px;
}

.navbar__list > li > a {
  color: white;
}

.Overview {
  width: 100%;
  display: flex;
  flex-direction: column;
  overflow: scroll;
  height: 100%;
  background-color: #ecf0f1;
}

.toolbar__heading {
  margin-top: 0;
}

.overview__toolbar__button:focus {
  outline: none;
}

.overview__toolbar__button--group:after {
  position: absolute;
  content: " ";
  border: 4px dashed white;
  border-radius: 5px;
  width: 18%;
  height: 60%;
}

.toolbar__heading {
  font-size: 3rem;
  font-family: sans-serif;
  text-align: left;
}

.Sidebar {
  width: 20%;
  max-width: 300px;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  list-style-type: none;
  padding: 0;
  margin: 0;
  overflow-y: scroll;
  min-width: 300px;
  position: relative;
  z-index: 1000;
  justify-content: flex-start;
  align-items: center;
  background-color: white; 
  border-radius: 5px 0 0 5px;
  overflow: scroll;
  box-shadow: -2px 0px 5px 0px rgba(120,120,120,1);
} 

.Sidebar__item {
  width: 100%;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0;
  height: 7.5vh;
  max-height: 80px;
  background-color: transparent;
  /*border-bottom: 1px solid #247EC2;*/
  box-sizing: border-box;
}

.fixture-button {
  width: 100%;
  height: 100%;
  padding: 0;
  background-color: transparent;
  border: 0;
  color: #212121;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  box-sizing: border-box;
}

.Sidebar__item:hover {
  cursor: pointer;
  background-color: #D7DBDC;
  box-sizing: border-box;
}

.fixture-button:hover {
  cursor: pointer;
}

.fixture-button:focus {
  outline: none;
}

.fixture-button * {
  margin: 5px 0;
  width: 50%;
  text-align: left;
}

.fixture-button__label {
  font-size: 1.4rem;
  width: 100%;
  text-align: left;
}

.editor {
  width: 100%;
  display: flex;
  flex-direction: row;
  height: 100vh; 
  min-height: 0;
  flex-grow: 1;
}

.editing-area {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  /*padding: 40px;*/
  height: 100vh;
  min-height: 0;
  overflow: hidden; 
  min-height: 0;
}

.toolbar {
  /*position: fixed;*/
  z-index: 100;
  width: 100px;
  min-width: 100px;
  background-color: white;
  top: 0;
  left: 0;
  box-sizing: border-box;
  /*padding: 20px 0;*/
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-radius: 5px;
  box-shadow: 1px 0px 5px 0px rgba(120,120,120,1);
}

.toolbar__button-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.toolbar__button {
  background-color: transparent;
  border: none;
  font-weight: bold;
  /*margin-bottom: 50px;*/
  height: 10vh;
  max-height: 80px;
  color: #3C3C3C; 
  font-size: 3rem;
}

.toolbar__button:hover {
  cursor: pointer;
  border-left: 3px solid #0D5E9A;
  color: #0D5E9A;
}

.toolbar__button:focus {
  outline: none;
}

.toolbar__button--add {
  
}

.toolbar__button--back {
  margin-bottom: 50px;
  border-bottom: 2px solid #eee;
}

.risers-container {
  flex-grow: 1;
  width: 90%;
  margin: 0 auto;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  overflow: visible;
  box-sizing: border-box;
  margin: 10px auto;
  overflow-x: hidden;
  padding: 5% 0;
}

.scroll-button {
  border-radius: 5px;
  border: none;
  font-size: 3rem;
  color: grey;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}

.scroll-button:hover {
  color: blue;
  cursor: pointer;
}

.scroll-button--right {
  position: fixed;
  z-index: 10000;
  right: 1.25%;
  top: 50%;
}

.scroll-button--left {
  position: fixed;
  z-index: 10000;
  left: 1.25%;;
  top: 50%;
}

.riser-wrapper {
  flex-grow: 1;
  background-color: #f7f7f7;
  overflow-y: scroll;
  background-color: #ecf0f1;
  padding: 30px;

}

.riser {
  display: flex;
  flex-direction: column-reverse;
  list-style-type: none;
  justify-content: center;
  flex-grow: 1;
  padding: 0;
  /*position: relative;*/
  /*z-index: 100;*/
  /*flex: 1 1 1000px;*/
  /*margin-bottom: 50px;*/
  overflow-y: scroll;
  margin: 0;
  box-sizing: border-box;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 1px 1px 5px 0px rgba(120,120,120,1);
  position: relative;
}

/*.riser-editing-area {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  overflow-y: hidden;
  position: relative;
  height: 100%;
}*/

.riser-list {
  display: flex;
  flex-direction: row;
  /*height: 70vh;*/
  list-style-type: none;
  padding: 0;
  justify-content: flex-start;
  /*height: 100%;*/
  box-sizing: border-box;
  /*margin-bottom: 20px;*/
  position: relative;
  top: 0;
  margin-top: 0;
  width: 100%;
  height: 100%;
  padding: 0 10px;
  /*top: 150px;*/
  align-self: flex-end;
  /*overflow-x: hidden;*/
  overflow-y: visible;
}

.floor-lines {
  width: 100%;
  position: absolute;
  top: 117px;
  box-sizing: border-box;
  overflow: hidden;
  /*margin-top: 10px;*/
  position: -webkit-sticky;
  position: sticky;
  left: 0;
  top: 0;
  height: 100%;
}

.floor-line {
  width: 100%;
  height: 100px;
  max-height: 100px;
  border-bottom: 1px solid lightgrey;
  box-sizing: border-box;
  max-height: 100px;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  position: relative;
  box-sizing: border-box;
}

.floor-line__label {
  margin: 0;
  margin-top: 1px;
  font-size: 1.5rem;
  color: #333;
  font-family: sans-serif;
  width: 75px;
  height: 100px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
  position: absolute;
  z-index: 5000;
}

.riser__toolbar-wrapper {
  width: 100%;
  height: 150px;
  display: flex;
  flex-direction: row;
  /*position: absolute;*/
  overflow-x: hidden;
  min-height: 150px;
  top: 10px;
  z-index: 1000;
}

.label__text {
  margin: 0;
}

.floor {
  border-bottom: 2px solid grey;
  display: flex;
  align-items: flex-end;
  min-height: 120px;
  /*height: 7.5vh;*/
  /*height: auto;*/
  /*height: 100%;*/
  position: relative;
  overflow-y: auto;
  overflow-x: hidden;
  flex-shrink: 0;
  box-sizing: border-box;
  background-color: #F4F5F5;
}

.floor--active {
  background-color: white;
  box-shadow: 0px 0px 5px 2px rgba(150,150,150,1);
  position: relative;
  z-index: 2;
  border-radius: 5px;
  -webkit-transform: scaleY(1.02);
          transform: scaleY(1.02);
}

.floor--source {
  border-bottom: 2px solid red;
}

.floor__total-info {
  width: 100px;
  /*height: 100%; */
  min-width: 100px;
  display: flex;
  flex-direction: column;
  align-self: stretch;
  box-sizing: border-box;
}

.floor__main {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  /*border-left: 4px solid red;*/
  height: 100%;
  width: 100%;
  min-height: 150px;
  padding: 10px 0;
  box-sizing: border-box;
  position: relative;
}

.floor--cold-source .floor-fixtures:after { 
  border-top: 4px solid blue;
  position: absolute;
  content: "";
  height: 10px;
  top: 10px;
  width: 100%;
  left: -5px;
}

.floor--hot-source .floor-fixtures:before { 
  border-top: 4px solid red;
  position: absolute;
  content: "";
  height: 10px;
  top: 20px;
  width: 100%;
  left: 5px;
}

.floor--cold .floor__main:after {
  border-left: 4px solid blue;
  height: 100%;
  width: 100%;
  position: absolute;
  content: " ";
  left: -5px;
}

.floor--hot .floor__main:before {
  border-left: 4px solid red;
  height: 100%;
  width: 100%;
  position: absolute;
  content: " ";
  left: 5px;
}

.floor__label {
  width: 100%;
  margin: 0;
  /*margin-top: 20px;*/
  align-self: flex-end;
  text-align: right;
}

.floor__input {
  width: 100%;
  text-align: right;
}

.floor__toolbar {
  /*position: absolute;
  right: 0;
  top: 0;*/
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  /*height: 100%;*/
  padding: 5px;
  box-sizing: border-box;
  width: 7vw;
  max-width: 120px;
  min-height: 100px;
  align-self: stretch;
  z-index: 10;
}

.floor__toolbar__button {
  font-size: 2rem;
  background-color: transparent;
  border: none;
  color: #ACAFB0;
}

.floor__toolbar__button:hover {
  cursor: pointer;
  color: #0D5E9A;
}

.floor__toolbar__button--hot:hover {
  color: red;
}

.floor--cold-source .floor__toolbar__button--cold {
  color: blue;
}

.floor--hot-source .floor__toolbar__button--hot {
  color: red;
}

.floor__total-info {
  /*height: 100%;*/
  min-height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  padding: 0 10px;
  padding-bottom: 5px;
  width: 5vw;
  max-width: 150px;
}

.floor__total-info__helper {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  justify-content: center;
  flex-grow: 1;
}

.floor__total-info__values {
  margin: 0;
  /*margin-bottom: 20px;*/
  font-size: 1.4rem;
}

.floor__total-info__column {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;  
  max-height: 120px;
}

.toolbar__info {
  display: flex;
  flex-direction: column;
}

.toolbar__info__text {
  margin: 0;
  font-size: 1.5rem;
}

.toolbar__info__text--cold {
  color: #3867d6;
}

.toolbar__info__text--hot {
  color: #d63031;
}

.toolbar__info__text--total {
  color: #6c5ce7;
}

.floor-fixtures {
  display: flex;
  flex-direction: row;
  height: 100%;
  /*width: 100%;*/
  flex-grow: 1;
  flex-wrap: wrap;
  padding-left: 20px;
  padding-top: 10px;
  position: relative;
  min-height: 100px;
}

.fixture {
  /*height: 100%;*/
  height: 7.5vh;
  max-height: 100px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin: 0 10px;
}

.fixture * {
  margin: 0;
}

.fixture__label {
  font-size: 1.6rem;
} 

.fixture__info {
  font-size: 2rem;
}
.Home {
	background-color: white;
	flex-grow: 1;
}

/*f6faff*/

.Home .lander {
  padding: 0;
  text-align: left;
  position: relative;	
	height: 100%;
	width: 100vw;
	
}

.Home .navbar {
	background-color: transparent;
}

.navbar__list li a {
	padding: 10px 20px;
	border-radius: 5px;
}

.navbar__link--signup a {
	background-color: #1dd1a1;
}

.navbar__link--login a {
	background-color: #8395a7;
}

.navbar__link:hover {
	-webkit-transform: scale(1.05);
	        transform: scale(1.05);
}

.navbar__link a:hover {
	text-decoration: none;
}

.lander__header {
	height: 50vw;
	max-height: 50vh;
	position: relative;
	background-image: url(https://res.cloudinary.com/rezdelamasa/image/upload/v1568779719/riser-sizer-bg.svg);
	background-repeat: no-repeat;
	background-size: cover;
	background-position: 60% 20%;
	display: flex;
	flex-direction: column;

}

.lander__header img {
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 0;
}

.lander__text {
	width: 90%;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	position: relative;
	z-index: 1;
	height: 35vw;
	justify-content: center;
}

.lander__heading {
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 5vw;
	margin: 0 auto;
  color: white;
  text-align: left;
  width: 100%;
}

.Home .lander__header p {
  font-size: 2.4vw;
  color: white;
  width: 100%;
	text-align: left;
	align-self: flex-start;
	margin-top: 2vw;
}

.lander__background {
	position: fixed;
	z-index: 0;
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
	width: 300%;
	top: -480%; left: -50%;
	background-repeat: repeat;
	background-position: bottom;
	background-size: 5%;
	height: 500%;
}

.lander__wrapper {
	width: 90%;
	margin: 0 auto;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	box-sizing: border-box;
	padding: 100px 0 100px 0;
}

.lander__column {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: flex-start;
	flex-grow: 1;
	box-sizing: border-box;
}

.lander__column--text {
	max-width: 40%;
}

.lander__image {
	margin: 0 auto;
	height: 100%;
	object-fit: contain;
	border-radius: 5px;
	overflow: hidden;
}

.dashboard {
	width: 100%;
	height: 100%;
	margin: 0 auto;
	display: flex;
}

.page-header {
	border: 0;
	margin: 0;
}

.page-header h1 {
	margin: 0;
}

.dashboard__content {
	padding-top: 80px;
	box-sizing: border-box;
	background-color: #ecf0f1;
	flex-grow: 1;
	padding: 80px 5% 0 5%;
}

.Menu__button--active {
	color: #4d83fe!important;
	text-decoration: none;
}

.subsection__header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 50px;
}

.dashboard__subheading {
	margin: 0;
	font-size: 3.2rem;
	font-weight: bold;
	color: #333;
}

.subsection__link {
	font-size: 1.8rem;
	padding: 10px 20px;
	border: 2px solid #6D99FE;
	/*background-color: #DEE8FE;*/
	box-sizing: border-box;
	border-radius: 50px;
	color: #6D99FE;
}

.subsection__link:hover {
	border: 2px solid #4d83fe;
	background-color: #4d83fe;
	text-decoration: none;
	color: white;
}

.projects__list {
	display: flex;
	margin-top: 30px;
	flex-wrap: wrap;
	justify-content: flex-start;
}

.list-group-item {
	/*width: 20vw;*/
	flex: 30% 0 1;
	height: 12vw;
	margin: 0 1.65%;
	border: none;
	border-radius: 10px;	
	margin-bottom: 3.3%;
	display: flex;
	flex-direction: column;
}

.list-group-item h4 {
	font-size: 2.2rem;
	margin-bottom: 1vh;
	margin-top: 10px;
	font-weight: bold;
	color: #444!important;
}

.list-group-item-text {
	display: flex;
	position: relative;
	flex-direction: column;
	flex-grow: 1;
	flex: 100% 1 1;
	justify-content: space-between;
	font-size: 1.4rem;
	color: #747B7D;
}

.project__address {
}

.project__date {
	position: relative;
	bottom: 0;
	align-self: flex-end;
	flex-grow: 1;
	flex: 100% 1 1;
	display: flex;
	align-items: flex-end;
	margin: 0;
	color: #8D9698;
	font-size: 1rem;
}

.list-group-item:hover {
	background-color: white!important;
	border-bottom: 4px solid #ccc;
	-webkit-transform: translateY(-4px);
	        transform: translateY(-4px);
	color: white!important;
}

.lander__row {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	flex-wrap: wrap;
	width: 90%;
	margin: 0 auto;
	margin-top: 100px;
}

.feature {
	display: flex;
	flex-direction: column;
	width: 100%;
	align-items: center;
	justify-content: space-between;
	margin: 0 auto;
	margin-bottom: 100px;
	flex-wrap: nowrap;
}

.feature__heading {
	color: #4D83FE;
	/*color: #333;*/
	font-size: 2.6rem;
	font-weight: bold;
	text-align: center;
}

.feature__icon {
	width: 30vw; height: 30vw;
	max-width: 300px;
	max-height: 300px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.feature__text {
	display: flex;
	justify-content: center;
	flex-direction: column;
	align-items: center;
}

.feature__description {
	text-align: center;
	font-size: 1.8rem;
	color: #444;
	max-width: 500px;
}

@media screen and (min-width: 768px) {
	.feature__text {
		justify-content: flex-start;
	}

	.feature__icon {
		width: 20vw;
		height: 20vw;
	}
}

@media screen and (min-width: 1200px) {

	.lander__heading {
		font-size: 7rem;
	}

 	.lander__row {
 		flex-direction: row;
 	}

	.feature {
		width: 50%;
	}

	.feature__icon {
		width: 10vw; height: 10vw;
	}

	.navbar__wrapper, .lander__text, .lander__row {
		max-width: 1200px;
	}

	.Home .lander__header p {
	  font-size: 4rem;
	}

}

.LoaderButton .spinning.glyphicon {
  margin-right: 7px;
  top: 2px;
  -webkit-animation: spin 1s infinite linear;
          animation: spin 1s infinite linear;
}
@-webkit-keyframes spin {
  from { -webkit-transform: scale(1) rotate(0deg); transform: scale(1) rotate(0deg); }
  to { -webkit-transform: scale(1) rotate(360deg); transform: scale(1) rotate(360deg); }
}
@keyframes spin {
  from { -webkit-transform: scale(1) rotate(0deg); transform: scale(1) rotate(0deg); }
  to { -webkit-transform: scale(1) rotate(360deg); transform: scale(1) rotate(360deg); }
}

.Login {
	flex-grow: 1;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  box-sizing: border-box;
  background-color: #63a4ff;
  position: relative;
} 

.Login__bg {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
}

.Login__header {
  display: flex;
  height: 70px;
  align-items: center;
  width: 90%;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

.Login__header a {
  font-size: 3rem;
  color: white;
  font-weight: bold;
  letter-spacing: -0.25rem;
}

.Login__content {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-top: 20vh;
  box-sizing: border-box;
  position: relative;
  z-index: 1;
}

.Login h1 {
  font-weight: bold;
}

.LoaderButton[disabled] {
  background-color: white;
  opacity: 1;
  border: none;
  font-weight: bold;
  color: #888;
  padding: 8px 25px;
}

.LoaderButton {
  background-color: white;
  opacity: 1;
  border: none;
  font-weight: bold;
  color: #666;
  padding: 8px 25px;
